<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item>首页</el-breadcrumb-item>
      <el-breadcrumb-item>问题管理</el-breadcrumb-item>
    </el-breadcrumb>

    <!--数据显示区域-->
    <el-card style="margin-top: 20px">
      <div style="margin: 15px 0;">问题列表</div>

      <el-row>
        <el-col :span="24">
          <!-- 新增按钮 -->
          <el-header style="background-color: #ffffff; margin-left: -5px">
            <el-form class="query-form" inline size="mini" label-width="120" :model="searchProduct">
              <el-form-item>
                <el-input placeholder="" class="input" @focus="" style="border-radius: 14px !important;">
                  <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
              </el-form-item>
            </el-form>
            <!-- <div style="width: 300px;float: right;">
              <el-button @click="handleSave()" icon="el-icon-circle-plus-outline">新增项目</el-button>
              <el-button @click="handleSave()" icon="el-icon-circle-plus-outline" type="warning" plain>批量删除</el-button>
            </div> -->
          </el-header>
          <!-- 项目详细信息 -->
          <el-table :data="dataList" style="width: 100%" class="select-from" :stripe="true">
            <ElTableColumn type="selection" />
            <el-table-column sortable width="100" prop="id" label="ID"> </el-table-column>
            <el-table-column align="center" sortable prop="name" label="项目名称"> </el-table-column>
            <el-table-column align="center" prop="type" label="问题类型">
              <template slot-scope="scope">
                <span>{{ scope.row.type }}<i class="co-icon-filter" /></span>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="300">
              <template slot-scope="scope">
                <el-button style="margin: 0 0 0 0" icon="el-icon-edit" size="mini" type="warning"
                  @click="setDialog(scope.row)" slot="reference">编辑
                </el-button>
                <el-popconfirm :title="'确定删除' + scope.row.name + '吗？'" @confirm="remove(scope.row.name)">
                  <el-button style="margin: 0 0 0 15px" icon="el-icon-close" size="mini" type="danger" slot="reference">删除
                  </el-button>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>

      <!--分页-->
      <el-row class="page-row">
        <el-pagination class="el-pagination" layout="total, sizes, prev, pager, next, jumper"
          :page-sizes="[5, 10, 30, 50]" @size-change="sizeChange" @current-change="currentChange"
          @prev-click="currentChange" @next-click="currentChange" :current-page="current" :page-size="size"
          :total="total">
        </el-pagination>
      </el-row>
    </el-card>

    <!-- 添加 -->
    <el-dialog title="添加项目" :visible.sync="saveDialog" width="800px">
      <el-form :model="productObj" ref="ruleForm" label-width="80px">
        <el-form-item label="项目名称" prop="title" :rules="[{ required: true, message: '名称不能为空' }]">
          <el-input v-model="productObj.title" maxlength="8" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="价格(分)" prop="price" :rules="[
          { required: true, message: '价格不能为空' },
          { type: 'number', message: '价格必须为数字值' },
        ]">
          <el-input v-model.number="productObj.price"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="width: 255px; margin: 45px 0 0 65px" @click="productAdd"
            :disabled="productObj.title === '' || productObj.price === null || productSet.price === ''">立即添加
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 详情 -->
    <el-dialog title="项目编辑" :visible.sync="updateDialog" width="600px">
      <span slot="title" class="dialog-footerB">
        <!-- <el-button size="mini" @click="goBack" icon="el-icon-arrow-left">项目详情</el-button> -->
        <div style="margin-top: 15px;">
          <div style="display: inline;display: flex;justify-content: space-between;">
            <span>项目名称:{{ productSet.name}}</span>
            <!-- <div style="width: 240px;float: right;">
              <el-input style="width: 100PX;margin-right: 20px;" type="text" size="mini" placeholder="问题类型"
                suffix-icon="el-icon-search"></el-input>
              <el-input style="width: 100PX;margin-right: 20px;" type="text" size="mini" placeholder="问题id"
                suffix-icon="el-icon-search"></el-input>
            </div> -->

          </div>
        </div>
        <hr />
      </span>
      <el-form :model="productSet" ref="ruleForm" label-width="80px">
        <el-form-item label="问题详情:" prop="description" rows="4">
          <el-input v-model="productSet.description" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="修正意见:" prop="fix" >
          <el-input v-model="productSet.fix" type="textarea" rows="4"></el-input>
        </el-form-item>
        <el-form-item label="代码范例:" prop="example" >
          <el-input v-model="productSet.example" type="textarea" rows="10"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" style="padding-left: 400px;">
        <el-button size='small' type="primary" @click="updateProduct">保存</el-button>
        <el-button size='small' @click="updateDialog = false">返回</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      adminName: "",
      // 搜索条件对象
      searchProduct: {
        id: "",
      },
      // 项目数据 (页面初始化时会将数据传进来)
      dataList: [
        {
          id: 'P001',
          name: '学生信息管理系统',
          type: '代码逻辑错误',
          description: '在处理学生信息时，系统出现了一个逻辑错误，导致学生的成绩无法正确地录入和显示。请分析代码并找出问题所在。',
          fix: '检查代码中的条件语句和循环语句，确保逻辑正确。可以使用调试工具或日志记录来定位问题所在。',
          example: `# 假设问题出在成绩录入环节，可以通过以下方式进行调试  
def add_grade(student_id, grade):  
    if student_id not in student_db:  # 如果学生ID不存在于数据库中  
        print("Invalid student ID")  # 输出错误提示信息  
        return  
    if grade < 0 or grade > 100:  # 如果成绩不在0到100之间  
        print("Invalid grade")  # 输出错误提示信息  
        return  
    student_db[student_id].add_grade(grade)  # 正常录入成绩  
  
# 在代码中加入调试语句，打印关键变量的值，以便定位问题所在  
print("student_id:", student_id)  
print("grade:", grade)`
        },
        {
          id: 'P002',
          name: '电子邮件客户端',
          type: '代码安全漏洞',
          description: '在处理电子邮件时，客户端代码存在一个安全漏洞，可能导致用户的账户信息和邮件内容被恶意攻击者获取。请进行代码分析并修复此漏洞。',
          fix: '对用户的输入进行验证和过滤，防止注入攻击。使用安全的API或库来处理邮件内容和账户信息。',
          example: `// 假设问题出在账户登录环节，可以通过以下方式进行修复  
public boolean login(String username, String password) {  
    if (username == null || password == null || username.isEmpty() || password.isEmpty()) {  
        return false;  // 输入验证，防止空输入或空字符串输入  
    }  
    String[] parts = username.split("@");  // 将用户名按照@分隔成两部分  
    if (parts.length != 2) {  
        return false;  // 输入验证，确保用户名格式正确  
    }  
    // 使用安全的API来验证用户名和密码，例如调用邮件服务商的API进行验证  
    boolean isValid = emailService.verifyAccount(username, password);  
    if (!isValid) {  
        return false;  // 如果验证失败，返回错误结果  
    }  
    // 正常登录成功，进行后续操作...  
}`
        },
        // 问题3...  
        {
          id: 'P003',
          name: '电子商务网站',
          type: '代码性能问题',
          description: '在处理大量用户订单时，网站的性能出现了瓶颈，导致用户无法正常下单或订单处理速度缓慢。请进行代码分析并优化性能。',
          fix: '使用缓存技术减少数据库访问次数，对数据库查询进行优化，考虑使用分布式架构来分担负载。',
          example: `// 假设问题出在数据库查询环节，可以通过以下方式进行优化  
function get_product($id) {  
    $cacheKey = "product_$id";  // 定义缓存键  
    $cachedData = memcached->get($cacheKey);  // 从缓存中获取数据  
    if ($cachedData !== false) {  
        return $cachedData;  // 如果缓存中有数据，直接返回  
    }  
    // 缓存中没有数据，从数据库中获取数据，并进行缓存  
    $data = dbQuery("SELECT * FROM products WHERE id=$id");  
    memcached->set($cacheKey, $data);  // 将数据存入缓存中  
    return $data;  
}`
        },
        // 问题4...  
        {
          id: 'P004',
          name: '在线视频播放器',
          type: '代码兼容性问题',
          description:  `在某些浏览器上，视频播放器无法正常工作，出现了兼容性问题。请进行代码分析并解决此问题。`,
          fix: '考虑不同浏览器对HTML5的支持程度，使用兼容性库或插件来解决问题，对视频格式进行转换或封装。',
          example: `// 假设问题出在视频格式兼容性上，可以通过以下方式进行解决  
var videoElement = document.createElement('video');  
videoElement.src = 'video.mp4';  // 设置视频源为MP4格式  
if (videoElement.canPlayType('video/ogg') !== '') {  
    videoElement.src = 'video.ogg';  // 如果浏览器支持OGG格式，则更换视频源为OGG格式  
}  
// 在视频播放器初始化时，根据浏览器的支持程度选择合适的视频格式进行播放  
if (videoElement.readyState > 3) {  
    videoElement.play();  // 如果浏览器支持播放视频，则开始播放  
} else {  
    // 如果浏览器不支持播放视频，则显示错误提示信息或使用备用播放器方案...  
}`
        },
        // 问题5...  
        {
          id: 'P005',
          name: '实时地图应用',
          type: '代码内存泄漏',
          description: '在长时间使用应用后，设备内存占用不断增加，导致应用运行缓慢甚至崩溃。请进行代码分析并找出内存泄漏的原因。',
          fix: `检查对象创建和销毁情况，避免重复创建相同的对象，及时释放不再使用的内存空间。使用内存分析工具来检测内存泄漏。`,
          example: `// 假设问题出在地图对象创建和销毁环节，可以通过以下方式进行修正  
public class MapManager {  
    private MapView mapView;  // 单例地图视图对象，全局唯一  
    private List<Marker> markerList;  // 标记点列表，用于存储多个标记点对象  
    public getMapView() {  
        if (mapView == null) {  
            mapView = new MapView();  // 创建地图视图对象，仅在第一次调用时创建一次  
        }  
        return mapView;  // 返回地图视图对象供其他组件使用  
    }  
    public addMarker(Marker marker) {  
        if (markerList == null) {  
            markerList = new ArrayList<Marker>();  // 初始化标记点列表对象仅创建一次即可重复使用对象内部空间来存储多个标记点对象markerList.add(marker);  // 将新的标记点加入列表中供后续使用或操作标记点对象可以重复使用不再需要的标记点对象应当及时从列表中移除并销毁以释放内存空间markerList.remove(marker);  // 从列表中移除不需要的标记点对象并销毁以释放内存空间当不再需要地图视图对象时应当及时销毁以释放内存空间mapView = null;  // 将地图视图对象设置为null以便垃圾回收器来回收其占用的内存空间当不再需要标记点列表对象时也应当及时销毁以释放内存空间markerList = null;  // 将标记点列表对象设置为null以便垃圾回收器来回收其占用的内存空间`
        },
      ],
      size: 5, // 每页的数据大小
      current: 1, //当前页数
      total: 0, // 总数量
      // 新增项目
      saveDialog: false, // 是否显示
      // 添加的项目对象
      productObj: {
        title: "",
        price: null,
      },
      // 编辑项目
      updateDialog: false, // 是否显示
      // 编辑的项目对象
      productSet: {
        id: null,
        title: "",
        price: null,
        example:'',
        fix:'',
        description:''
      },
      // option
      filterArr: [
        { text: "评级A", value: "A" },
        { text: "评级B", value: "B" },
        { text: "评级C", value: "C" },
      ],
      filterArr2: [
        { text: "C", value: "下拉1" },
        { text: "C++", value: "下拉2" },
        { text: "C#", value: "下拉2" },
        { text: "JAVA", value: "下拉2" },
      ],


      // 项目详情
      goBack() {
      }
    };
  },
  created() {
    this.getData();
  },
  methods: {
    // 添加框
    handleSave() {
      this.saveDialog = true;
    },
    // 编辑框
    setDialog(item) {
      this.productSet=item;
      this.updateDialog = true;
    },
    filterChange(filters) {
      // 判断是哪一列筛选
      if (filters.id) {
        //  赋值这一列绑定的值
        this.dataList.id = filters.id[0];
      }
      //  this.getTableList(); // 通过接口获取数据的方法
    },
    // 删除
    remove(name) {
      this.dataList = this.dataList.filter(
        item => item.name !== name
      )
     },
    // 立即添加
    productAdd() { },
    // 更新项目信息
    updateProduct() {
      this.updateDialog= false;
    },
    // 每页大小修改
    sizeChange(val) {
      this.size = val;
      if (this.searchProduct.productName !== "") {
        this.search();
      } else {
        this.getData();
      }
    },
    // 根据搜索条件查询
    search() {
      if (this.searchProduct.productName !== "") {
      } else {
        this.$message.error("搜索的项目名不能为空");
      }
    },
    // 第几页
    currentChange(val) {
      this.current = val;
      if (this.searchProduct.productName !== "") {
        this.search();
      } else {
        this.getData();
      }
    },
    // 获取数据，并赋值给wordData
    getData() { },
  },
};
</script>

<style>
.page-row {
  margin-top: 20px;
  text-align: center;
}

.header2 {
  color: #004b54;
  font-size: 20px;
  position: absolute;
  left: 10px;
  top: 5px;
}

.action-groups {
  display: inline-block;
}


/*搜索组件最外层div */
.input_box {
  width: 300px;
  margin-right: 15px;
  border-radius: 95px;
  background: rgb(0 234 245 / 48%);
}

/*搜索input框 */
:deep(.el-input__wrapper) {
  background-color: transparent;
  /*覆盖原背景颜色，设置成透明 */
  border-radius: 95px;
  border: 0;
  box-shadow: 0 0 0 0px;
  width: 100px;
}

/*搜索button按钮 */
:deep(.el-input-group__append) {
  background: rgb(0 234 245 / 48%);
  border-radius: 95px;
  border: 0;
  box-shadow: 0 0 0 0px;
}
</style>